import React, { Component } from "react";

class Projects extends Component {
  state = {
    project1:
      "https://raw.githubusercontent.com/bhupathi189/bhupathi189.github.io/master/src/Components/project1.jpg",
    Website: "website1",
  };
  render() {
    return (
      <div className="content-wrap">
        <React.Fragment>
          <h1>Projects</h1>
        </React.Fragment>
        <div className="flex-container">
          {this.project(this.state.project1, this.state.Website)}
          {this.project("https://picsum.photos/400/300", "website2")}
          {this.project("https://picsum.photos/400/300", "webiste3")}
          {this.project("https://picsum.photos/400/300", "website4")}
          {this.project("https://picsum.photos/400/300", "website5")}
        </div>
      </div>
    );
  }
  project(imgurl, project_name) {
    return (
      <div className="grid-items">
        <img src={imgurl} alt="img" className="project-image"></img>
        <span className="project-name">{project_name}</span>
        <div className="project-details">
          <p>project details</p>
        </div>
      </div>
    );
  }
}

export default Projects;
